}

.header_button button{
  margin-left: 0;
  margin-bottom: 2vw;
  margin-left: 2vw;
  padding: 0;
  width: 12vw;
  height: 7vw;
  border-top: 2px;
  border-bottom: 2px;
  border-right: 2px;
  color: white;
  border-color: white;
  -webkit-transform: skewX(150deg);
  -moz-transform: skewX(150deg);
  transform: skewX(150deg);
}

.header_button img{
  margin-left: 5vw;
  width: 12vw;
  height: auto;
  margin-top: 2vw;
  border-radius: 65px;
  box-shadow: 0 5px 0 #000000;
	transition: .2s;
}

.header_button img:hover{
  transform: translateY(5px);
}

.header_button img:active{
  transform: translateY(20px);
}

