.Log_article{
  width: 90vw;
  font-size: 1.99489vw;
  background-color: #DDD;
  margin:2em 0;
  position: relative;
  padding: 0.5em 1.5em;
  border-top: 3px solid black;
  border-bottom: 3px solid black;
}

.Log_article:before, .Log_article:after{
    content: "";
    position: absolute;
    top: -10px;
    width: 2px;
    height: -webkit-calc(100% + 20px);
    height: calc(100% + 20px);
    background-color: black;
}
.Log_article:before {left: 10px;}
.Log_article:after {right: 10px;}
.Log_article p {
    margin: 0;
    padding: 0;
}

